.new-expense__controls{
    display: flex;
    flex-wrap: wrap;
    margin-left: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
} 

.new-expense__control label{
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}

.new-expense__control input{
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 22rem;
    max-width: 100%;
}
.new-expense__action button {
    font: inherit;
    cursor:pointer ;
    width: 22rem;
    max-width: 100%;
    margin-top: 1rem;
    padding:1rem 2rem;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
}